<script>
import liquidacion from '@/components/calculo/liquidacionRapida'
import IconBase from '@/components/IconBase'
import IconBancariaCorriente from '@/components/icons/bancariacorriente'
import IconBancariaMensual from '@/components/icons/bancariamensual'
import IconUsuraEA from '@/components/icons/usura-e-a'
import IconUsuraMensual from '@/components/icons/usuramensual'
import IconIPC from '@/components/icons/ipc'

export default {
  components: {
    liquidacion,
    IconBase,
    IconBancariaCorriente,
    IconBancariaMensual,
    IconUsuraEA,
    IconUsuraMensual,
    IconIPC,
  },
    data: () =>({
        labels: [
        'Ene',
        'Feb',
        'Mar',
        'Abr',
        'May',
        'Jun',
        'Jul',
        'Ago',
        'Sep',
        'Oct',
        'Nov',
        'Dic',
      ],
      value: [],
      value2:[],
      datos:[],
    }),
    mounted () {
        this.obtener()
    },
    methods:{
        onInput (val) {
        this.steps = parseInt(val)
      },
      nextStep (n) {
        if (n === this.steps) {
          this.verifica()
        } else {   
          if(n==4){
            this.etiqueta="Finalizar"
          }
          this.e1 = n + 1      
        }
      },
      lastStep (n) {
        if (n === this.steps) {
          this.etiqueta="siguiente"
          this.e1 = n - 1
        } else {   
          this.e1 = 1      
        }
      },
        async obtener(){
            this.axios.defaults.headers.common["Authorization"]= 'Bearer ' + localStorage.userToken
            await this.axios.get('/lista/tasas')
            .then(res => {
                this.datos =res.data['data'][0]
                this.value = res.data['data'][1]
                this.value2 = res.data['data'][2]
            })
            .catch(e => {
                if (e.response.data['msg']=="Token has expired"){
                    var msg = "Debe de cerrar la sessión ya que por seguridad el token expira"
                }else{
                    msg = e.response.data['msg']
                }
                this.$swal({icon: 'warning',
                            title: 'Atención..!!!',
                            text:msg,
                            showConfirmButton: true,
                            });
            })
        },
    },
}
</script>