<template>
  <!-- <v-container fluid> -->
    <Graficas v-if="privilegios==false"/>
    <Admin v-else/>
  <!-- </v-container> -->
</template>

<script>
  import Graficas from '../components/calculo/graficas.vue'
  import Admin from '@/components/admin/admin.vue'
  import jwtDecode from 'jwt-decode'

  export default {
    name: 'Home',

    components: {
      Graficas,
      Admin,
    },
    data:()=>({
      privilegios:false,
    }),
    mounted(){
      const token = localStorage.userToken
      const decoded = jwtDecode(token)
      var rol = decoded.identity.privilegios
      if (rol == "1.1.1.1.1"){
        this.privilegios = true
      }
    },
  }
</script>
