<template>
    <v-container sm>
        <p class="font-weight-light" style="font-size:25px;" title-1>Captura Taza Intereses</p>
        <v-divider></v-divider>
        <br>
        <v-form v-model="valid">
            <v-card>
                <v-card-title>
                    <v-row align-content="center">
                        <v-spacer></v-spacer>
                        <v-col cols="6" sm="3" md="4">
                            <v-menu
                                ref="menu"
                                v-model="menu"
                                :close-on-content-click="false"
                                :return-value.sync="date"
                                transition="scale-transition"
                                offset-y
                                max-width="290px"
                                min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="date"
                                    label="Elije un mes"
                                    prepend-icon="mdi-calendar"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                                </template>
                                <v-date-picker
                                v-model="date"
                                type="month"
                                no-title
                                scrollable
                                >
                                <v-spacer></v-spacer>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="menu = false"
                                >
                                    Cancel
                                </v-btn>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="$refs.menu.save(date)"
                                >
                                    OK
                                </v-btn>
                                </v-date-picker>
                            </v-menu>
                        </v-col>
                        <v-spacer></v-spacer>
                    </v-row>
                </v-card-title>
                <v-card-text>
                    <v-row align-content="center">
                        <v-spacer></v-spacer>
                        <v-col cols="12" sm="6" md="4">
                            <v-text-field
                                label="BC_EA"
                                outlined
                                suffix="%"
                                clearable
                                :rules="rules"
                                v-model="bc_ea"
                                :loading="loading2"
                                :disabled="loading2"
                                autofocus
                            ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                            <v-text-field
                                label="BC_MENSUAL"
                                outlined
                                suffix="%"
                                clearable
                                :rules="rules"
                                v-model="bc_mensual"
                                :loading="loading2"
                                :disabled="loading2"
                            ></v-text-field>
                        </v-col>
                        <v-spacer></v-spacer>
                    </v-row>
                </v-card-text>
                <v-card-text>
                    <v-row align-content="center">
                        <v-spacer></v-spacer>
                        <v-col cols="12" sm="6" md="4">
                            <v-text-field
                                label="USURA_EA"
                                outlined
                                suffix="%"
                                clearable
                                :rules="rules"
                                v-model="usura_ea"
                                :loading="loading2"
                                :disabled="loading2"
                            ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                            <v-text-field
                                label="USURA_MENSUAL"
                                outlined
                                suffix="%"
                                clearable
                                :rules="rules"
                                v-model="usura_mensual"
                                :loading="loading2"
                                :disabled="loading2"
                            ></v-text-field>
                        </v-col>
                        <v-spacer></v-spacer>
                    </v-row>
                    <v-row align-content="center">
                        <v-spacer></v-spacer>
                        <v-col cols="12" sm="6" md="4">
                            <v-text-field
                                label="IPC"
                                outlined
                                suffix="%"
                                clearable
                                v-model="ipc"
                                :rules="rules"
                                :loading="loading2"
                                :disabled="loading2"
                            ></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="4" v-if="valid">
                            <v-btn
                                class="ma-4"
                                :loading="loading2"
                                :disabled="loading2"
                                :color="color"
                                @click="revisar"
                                block
                                >
                                Guardar
                                <template v-slot:loader>
                                    <span>Agregando...</span>
                                </template>
                            </v-btn>
                        </v-col>
                        <v-spacer></v-spacer>
                    </v-row>
                </v-card-text>
            </v-card>
        </v-form>
        <div>
            <br>
            <br>
            <br><br><br>
        </div>
    </v-container>
</template>
<script>
export default {
    data () {
      return{
          valid: false,
          date: new Date().toISOString().substr(0, 7),
          loading2: false,
          loader: null,
          menu: false,
          color:"success",
          bc_ea:null,
          bc_mensual:null,
          usura_ea:null,
          usura_mensual:null,
          ipc:null,
          rules:[
              value => !!value || 'Campo obligatorio',
              value => {
                  const pattern =/^\d*(\.\d{1,4})?$/
                  return pattern.test(value) || 'Solo numeros o 4 decimales'
              }
          ],
      }
    },
    methods:{
        async revisar(){
            this.loading2=true
            let lista = [this.date, this.bc_ea, this.bc_mensual, this.usura_ea, this.usura_mensual, this.ipc]
            this.axios.defaults.headers.common["Authorization"]= 'Bearer ' + localStorage.userToken
            await this.axios.post('/admin/captura/tazas', lista)
            .then(res=>{
                if (res){
                    this.$swal({icon: 'success',
                                title: 'Felicidades!!',
                                text: res.data.data,
                                showConfirmButton: false,
                                timer: 4000,
                            })
                }
                setTimeout(() => (
                this.loading2 = false), 3000)
                this.loader = null

            })
            .catch(error => {
                this.loading2 = false
                this.loader = null
                this.$swal({icon: 'error',
                                title: 'Error',
                                text: error,
                                showConfirmButton: false,
                                timer: 4000,
                            })
            })
            
        },
    },
    
}
</script>
