function verificarFecha(fecha){
    var fechaf = fecha.split("-");
    var day = fechaf[2];
    var month = fechaf[1];
    var year = fechaf[0];
    var date = new Date(year, month, '0');
    if (month > 12 || month <= 0){
        return false;
    }
    if ((day-0)>(date.getDate()-0)){
        return false;
    }
    return true;
}

export default verificarFecha;