</template>>
<script>
import checkDate from '@/tools.js';
export default {
    data () {
      return {
        dialog:false,
        deuda:"",
        fIni:"",
        fFin:"",
        tasapac:"",
        tasa:"",
        items: ['BC_MENSUAL','IPC','USURA_MEN', 'OTRA'],
        e1: 1,
        activar:true,
        steps: 4,
        etiqueta:"Siguiente",
        labels:["Ingrese el Valor", "Indique las Fechas", "Seleccione la tasa", "Liquide"],
        rules:[
            value => !!value || 'Campo Obligatorio',
        ],
        leer:true,
        desserts:[],
        headers: [
          {
            text: 'Fecha',
            align: 'start',
            sortable: true,
            value: 'fecha',
          },
          { text: 'Tasa', value: 'tasa' },
          { text: 'Interes ($)', value: 'interes' },
          { text: 'Total ($)', value: 'total' },
          { text: 'Capital ($)', value: 'capital' },
        ],
        totales:[],
        algo:[],
      }
    },

    methods: {
        nextStep (n) {
            if (n === this.steps-1) {
                this.etiqueta="Finalizar"
                this.e1+=1
            } else if (n<=3){
                if (n==1){
                    if(this.deuda.length>0){
                        this.e1 = n + 1
                        this.activar = false
                    }
                }
                if (n==2){
                    if(this.fIni.length>0 && this.fFin.length>0){
                        if (checkDate(this.fIni) && checkDate(this.fFin)){
                        this.e1 = n + 1
                        this.activar = false
                        }
                    }
                }
                if (n==3){
                    if(this.tasa.length>0){
                        this.e1 = n + 1
                        this.activar = false
                    }
                }
            }else if(this.etiqueta=="Finalizar"){
                let envio = ['',this.deuda, this.fIni, this.fFin, this.tasa, this.tasapac]
                this.enviar(envio)
            }
      },
        lastStep (n) {
            if (n==2){
                this.activar=true
                this.e1 = n - 1
            }else if(n>1){
                this.e1 = n - 1
                this.etiqueta="Siguiente"
                
            }
      },
        async enviar(recibo){
          this.axios.defaults.headers.common["Authorization"]= 'Bearer ' + localStorage.userToken
            await this.axios.post('/amortizacion/calculo', [recibo,1])
            .then(res => {
                this.dialog = true
                this.desserts=res.data['data']
                this.totales = res.data['totales']
                res.data['complemento'].forEach(element => {
                this.algo.push({periodo:element[0], dIni:element[1], dIniC:element[2], Dfin: element[3], DfinC:element[4]})
                });
                this.leer=false
            })
            .catch(error => {})
      }
    },
  }
</script>>